.container {
  background: white;
  margin: 12px;
  padding: 12px;
  border: 1px solid #f0f0f0;
  border-radius: 4px;
  overflow: auto;
}
.container .wrap {
  background: white;
}
.container .wrap .searchForm {
  box-shadow: none;
}
.container .item {
  height: 50px;
  border-bottom: 1px solid #e6e6e6;
}
.container .demoImg {
  display: block;
  width: auto;
  height: 300px;
}
.container .blockContainer {
  width: 400px;
  overflow: hidden;
  display: flex;
  column-gap: 20px;
  row-gap: 20px;
  flex-wrap: wrap;
  border: 1px solid #eee;
}
.container .blockContainer .block {
  min-width: 80px;
  flex: 1;
  height: 50px;
  border: 1px dashed #eee;
}
.count {
  color: red;
}
